import React from 'react'
import styled from 'styled-components'
import { AppLink } from './gatsbyWithStyled'
import { rhythm } from '../utils/typography'

const ExcerptTitle = styled.h3`
  margin-bottom: ${rhythm(1 / 4)};
  color: #007acc;
`

const ExcerptDate = styled.small`
  margin-right: 10px;
  color: #666;
`

const ExcerptTag = styled.small`
  margin-right: 10px;
  color: #666;
  cursor: pointer;
`

const Excerpt = (props) => (
  <div>
    <ExcerptTitle>
      <AppLink style={{ boxShadow: 'none' }} to={props.to}>
        {props.title}
      </AppLink>
    </ExcerptTitle>

    <ExcerptDate>{props.date}</ExcerptDate>

    <small>标签：</small>

    {
      props.tags.map(item => (
        <ExcerptTag key={item}>
          <AppLink style={{ boxShadow: 'none' }} to={`/tags/${item}`}>
            {item}
          </AppLink>
        </ExcerptTag>
      ))
    }

    <p dangerouslySetInnerHTML={{ __html: props.excerpt }} />
  </div>
)

export default Excerpt